import React, { PureComponent } from "react";
import { CloudUrl } from "../../../constants";
import { getQueryString, getCurrentTime, APPEND_CHILD, wUrl } from '../../../utils'
import { suiteTrack, TagShopEditor, isTagbox, isEmbed, isSocialWall, DOMAIN, isEditor } from "../../../actions/api";
import { StyleConstant } from "./StyleConstant";
const defaultURL = `https://cloud.taggbox.com/wall-branding/wallId_widget_`
const currentTime = getCurrentTime(1) + 1000 * (getCurrentTime(4) + 60 * (getCurrentTime(2) + 60 * getCurrentTime(3)));


const InHeritStyleRender = (wall) => {
    const { cardStyle, ctaBtn, button, popUpProperties, ProductSettings, heading, subheading, ticker, height, reviewTitle, reviewBtn, reviewStar } = StyleConstant(wall)
    const styleId = `style-root-${wall.ThemeInfo.wallId}`;
    const { QrCode } = wall.SnapUp
    const isRendered = isEditor ? false : document.querySelectorAll('[data-wall-id]')?.length > 0 || document.querySelectorAll('[data-widget-id]')?.length > 0;
    const rootStyle = `${isRendered ? `[data-wall-id],[data-widget-id]` : `:root,[data-widget-id]`} {
                        --tb-color: ${cardStyle.color};
    --tb-font-size:${cardStyle.fontSize};
    --tb-font: ${cardStyle.font};
    --tb-font-weight: ${cardStyle.fontWeight};
    --tb-font-tyle: ${cardStyle.fontStyle};
    --tb-icon-color:${cardStyle.iconColor};
    --tb-line-height: ${cardStyle.lineHeight};
    --tb-link-color: ${cardStyle.linkColor};
    --tb-bg-color: ${cardStyle.background};
    --tb-background-size: contain;
    --tb-author-color: ${cardStyle.authorColor};
    --tb-author-font: ${cardStyle.authorFont};
    --tb-author-font-weight: ${cardStyle.authorFontWeight};
    --tb-author-font-style: ${cardStyle.authorFontStyle};
    --tb-border-radius: ${cardStyle.radius};
    --tb-icon-position:${cardStyle.iconPosition};
    --tb-media-size:${cardStyle.cardSize};
    --tb-padding:${cardStyle.cardPadding};
    --tb-grid-row:${cardStyle.rows};

    --tb-cta-color:${ctaBtn.color};
    --tb-cta-bg-color:${ctaBtn.background};
    --tb-cta-border-color:${ctaBtn.borderColor};
    --tb-cta-color-hover:${ctaBtn.colorHover};
    --tb-cta-bg-color-hover:${ctaBtn.backgroundHover};
    --tb-cta-border-color-hover:${ctaBtn.borderHover};
    --tb-cta-border-radius:${ctaBtn.radius};
    --tb-cta-padding:${ctaBtn.padding};
    --tb-cta-height:${ctaBtn.minHeight};
    --tb-cta-width:${ctaBtn.minWidth};
    --tb-cta-font:${ctaBtn.font};
    --tb-cta-font-weight:${ctaBtn.weight};
    --tb-cta-transition:${ctaBtn.transition};

    --tb-btn-color:${button.color};
    --tb-btn-bg-color:${button.background};
    --tb-btn-border-color:${button.borderColor};
    --tb-btn-color-hover:${button.colorHover};
    --tb-btn-bg-color-hover:${button.backgroundHover};
    --tb-btn-border-color-hover:${button.borderHover};
    --tb-btn-border-radius:${button.radius};
    --tb-btn-padding:${button.padding};
    --tb-btn-height:${button.minHeight};
    --tb-btn-width:${button.minWidth};
    --tb-btn-font:${button.font};
    --tb-btn-font-weight:${button.weight};
    --tb-btn-transition:${button.transition};

    --tb-popup-color:${popUpProperties.color};
    --tb-popup-bg-color:${popUpProperties.background};
    --tb-popup-font:${popUpProperties.font};
    --tb-popup-font-size:${popUpProperties.fontSize};
    --tb-popup-icon-color:${popUpProperties.iconColor};
    --tb-popup-font-weight:${popUpProperties.fontWeight};
    --tb-popup-font-style: ${popUpProperties.fontStyle};

    --tb-popup-author-font:${popUpProperties.authorFont};
    --tb-popup-author-font-size:${popUpProperties.authorFontSize};
    --tb-popup-author-font-weight:${popUpProperties.authorfontWeight};
    --tb-popup-author-font-style:${popUpProperties.authorfontWeight};
    --tb-popup-author-color:${popUpProperties.authorColor};


    --tb-catalogue-font:${ProductSettings.CatalogueTitle.font};
    --tb-catalogue-varient:${ProductSettings.CatalogueTitle.varient};
    --tb-catalogue-weight:${ProductSettings.CatalogueTitle.weight};
    --tb-catalogue-size:${ProductSettings.CatalogueTitle.size};
    --tb-catalogue-color:${ProductSettings.CatalogueTitle.color};

    --tb-product-font:${ProductSettings.ProductTitle.font};
    --tb-product-varient:${ProductSettings.ProductTitle.varient};
    --tb-product-size:${ProductSettings.ProductTitle.size};
    --tb-product-color:${ProductSettings.ProductTitle.color};

    --tb-price-font:${ProductSettings.Price.font};
    --tb-price-weight:${ProductSettings.Price.weight};
    --tb-price-varient:${ProductSettings.Price.varient};
    --tb-price-size:${ProductSettings.Price.size};
    --tb-price-color:${ProductSettings.Price.color};

    --tb-shop-btn-font:${ProductSettings.Button.font};
    --tb-shop-btn-weight:${ProductSettings.Button.weight};
    --tb-shop-btn-varient:${ProductSettings.Button.varient};
    --tb-shop-btn-size:${ProductSettings.Button.size};
    --tb-shop-btn-color:${ProductSettings.Button.color};
    --tb-shop-btn-bg-color:${ProductSettings.Button.background};

    --tb-hotspot-color:${ProductSettings.Hotspot.color};
    --tb-hotspot-bg-color:${ProductSettings.Hotspot.background};

    --tb-su-btn-font:${QrCode?.style?.font};
    --tb-su-btn-varient:${QrCode?.style?.varient};
    --tb-su-btn-weight:${QrCode?.style?.weight};
    --tb-su-btn-size:${QrCode?.style?.size};
    --tb-su-btn-color:${QrCode?.style?.color};
    --tb-su-btn-background:${QrCode?.style?.background};

    --tb-t-font-size:${heading.title.size};
    --tb-t-font-family:${heading.title.font};
    --tb-t-font-weight:${heading.title.fontWeight};
    --tb-t-color:${heading.title.color};
    --tb-t-text-align:${heading.title.textalign};

    --tb-st-font-size:${subheading.subtitle.size};
    --tb-st-font-family:${subheading.subtitle.font};
    --tb-st-font-weight:${subheading.subtitle.fontWeight};
    --tb-st-color:${subheading.subtitle.color};
    --tb-st-text-align:${subheading.subtitle.textalign};

    --tb-ti-title-font-size:${ticker.heading.size};
    --tb-ti-title-font-family:${ticker.heading.font};
    --tb-ti-title-font-weight:${ticker.heading.fontWeight};
    --tb-ti-title-color:${ticker.heading.color};
    --tb-ti-title-background:${ticker.heading.background};
    --tb-ti-desc-font-size:${ticker.desc.size};
    --tb-ti-desc-font-family:${ticker.desc.font};
    --tb-ti-desc-font-weight:${ticker.desc.fontWeight};
    --tb-ti-desc-color:${ticker.desc.color};
    --tb-ti-desc-background:${ticker.desc.background};
    --tb-theme-height:calc(100vh - ${height.banner} - ${height.ticker});
    --tb-banner-height:${height.banner};
    --tb-ticker-height:${height.ticker};
    
    --tb-re-title-font:${reviewTitle.font};
    --tb-re-title-varient:${reviewTitle.variant};
    --tb-re-title-size:${reviewTitle.size};
    --tb-re-title-color:${reviewTitle.color};

    --tb-btn-review-font:${reviewBtn.font};
    --tb-btn-review-varient:${reviewBtn.variant};
    --tb-btn-review-size:${reviewBtn.size};
    --tb-btn-review-color:${reviewBtn.color};
    --tb-btn-review-bg-color:${reviewBtn.background};

    --tb-review-start-color:${reviewStar.color};

    }`
    if (!document.getElementById(styleId)) APPEND_CHILD(rootStyle, 'style', styleId)

}
const onLoadRemoveCss = (wallId) => {
    try { [`#style-root-${wallId}`, `#css-${wallId}`].forEach(selector => document.querySelector(selector)?.remove()); }
    catch (ex) { }
}
const customCssAdd = (wall) => {
    const styleId = `css-${wall.ThemeInfo.wallId}`;
    if (!document.getElementById(styleId) && wall.ThemeStyle.css.status === 1) APPEND_CHILD(wall.ThemeStyle.css.code, 'style', styleId)
}
const addCustomLang = () => {
    try {
        const parsed = window.location.href;
        var htmlElement = document.documentElement;
        if ((parsed && getQueryString(window.location.href, 'lang'))) htmlElement.setAttribute('lang', getQueryString(window.location.href, 'lang'));
        else htmlElement.setAttribute('lang', 'de');
    }
    catch (e) {
        console.error(e)
    }
}
class AppHead extends PureComponent {
    constructor(props) {
        super(props)
        this.state = {
            HASHTAG_CAMP_IMG: props.wall?.ThemeInfo.wallId && wUrl("hashtag_campaign") ? `${defaultURL}${props.wall?.ThemeInfo.wallId}.jpg?v=${currentTime}` : "",
        }

    }

    onLoadModule = (props) => {
        const isHastag = wUrl("hashtag_campaign") ? false : false
        const { wall } = props;
        if (isHastag) this.setState({ HASHTAG_CAMP_IMG: `${CloudUrl}widget/assets/media/theme/themeThumb${wall?.ThemeInfo.id}.png` })
    }
    componentDidMount() {
        this.onLoadModule(this.props)
    }
    UNSAFE_componentWillReceiveProps(newProps) {
        if (newProps != this.props && TagShopEditor) {
            onLoadRemoveCss(newProps?.wall?.Wall?.id)
            this.onLoadModule(newProps)
        }
        else if (newProps != this.props && wUrl("addfeedload=load")) {
            onLoadRemoveCss(newProps?.wall?.Wall?.id)
            this.onLoadModule(newProps)
        }
    }
    render() {
        const { HASHTAG_CAMP_IMG } = this.state;
        const { wall } = this.props;
        const { CardStyle, ThemeInfo, ProductSetting, Banner } = wall
        const { QrCode } = wall.SnapUp
        const { Ticker } = wall?.Ticker ? wall?.Ticker : []
        const wallName = ThemeInfo.wallName
        const brandName = suiteTrack ? `Tagbox UGC Suite` : isSocialWall ? ` Live SocialWall - Tagbox` : `Taggbox`
        const domains = [DOMAIN.TAGSHOP, DOMAIN.TAGSHOP_TEST, DOMAIN.TAGBOX, DOMAIN.TAGBOX_TEST, DOMAIN.TAGEMBED, DOMAIN.TAGEMBED_TEST, DOMAIN.SOCIALWALL, DOMAIN.SOCIALWALL_TEST, DOMAIN.SOCIALEMBED]
        const matchDomain = domains.some((url) => wUrl(url))

        const hashtagObj = [
            {
                property: 'type',
                content: 'website'
            },
            {
                property: 'url',
                content: `${DOMAIN.TAGBOX_LITE}${ThemeInfo.wallId}`
            },
            {
                property: 'title',
                content: `${wallName} - ${brandName} Widget`
            },
            {
                property: 'description',
                content: `${wallName} hashtag campaign created using ${brandName} Widget.`
            },
            {
                property: 'image',
                content: HASHTAG_CAMP_IMG
            }
        ]
        const widgetMetaObj = [
            {
                property: 'type',
                content: 'website'
            },
            {
                property: 'url',
                content: `${DOMAIN.TAGBOX_LITE}/${ThemeInfo.wallId}`
            },
            {
                property: 'title',
                content: `${wallName} - ${brandName} Widget`
            },
            {
                property: 'description',
                content: `${wallName} featuring Social Feed using ${brandName} Widget. It helps brands to curate & embed social feeds, user reviews, UGC etc. on websites to boost sales, traffic, & engagement.`
            }
        ]

        var isNotFonts = ['inherit', 'Default', '0']
        var allFonts = ['Inter'];

        if (!CardStyle.inheritStyles && CardStyle.style.fontLink && !isNotFonts.includes(CardStyle.style.fontLink)) allFonts.push(CardStyle.style.fontLink)

        if (!CardStyle.inheritStyles && CardStyle.author.fontLink && !isNotFonts.includes(CardStyle.author.fontLink)) allFonts.push(CardStyle.author.fontLink)

        if (Banner && Object.keys(Banner).length) {
            if (Banner.subTitle.style.fontLink && !isNotFonts.includes(Banner.subTitle.style.fontLink)) allFonts.push(Banner.subTitle.style.fontLink)
            if (Banner.title.style.fontLink && !isNotFonts.includes(Banner.title.style.fontLink)) allFonts.push(Banner.title.style.fontLink)
        }

        if (!CardStyle.inheritStyles && ProductSetting && Object.keys(ProductSetting).length) {
            if (ProductSetting.Button.fontLink && !isNotFonts.includes(ProductSetting.Button.fontLink)) allFonts.push(ProductSetting.Button.fontLink)
            if (ProductSetting.CatalogueTitle.fontLink && !isNotFonts.includes(ProductSetting.CatalogueTitle.fontLink)) allFonts.push(ProductSetting.CatalogueTitle.fontLink)
            if (ProductSetting.Price.fontLink && !isNotFonts.includes(ProductSetting.Price.fontLink)) allFonts.push(ProductSetting.Price.fontLink)
            if (ProductSetting.ProductTitle.fontLink && !isNotFonts.includes(ProductSetting.ProductTitle.fontLink)) allFonts.push(ProductSetting.ProductTitle.fontLink)
        }

        if (!CardStyle.inheritStyles && QrCode && Object.keys(QrCode).length) {
            if (QrCode && QrCode.style.fontLink && !isNotFonts.includes(QrCode.style.fontLink)) allFonts.push(QrCode.style.fontLink)
        }

        if (Ticker && Object.keys(Ticker).length) {
            if (Ticker.title.style.fontLink && !isNotFonts.includes(Ticker.title.style.fontLink)) allFonts.push(Ticker.title.style.fontLink)
            if (Ticker.subTitle.style.fontLink && !isNotFonts.includes(Ticker.subTitle.style.fontLink)) allFonts.push(Ticker.subTitle.style.fontLink)
        }
        allFonts = allFonts.filter((value, index, self) => {
            return self && self.indexOf(value) === index;
        });
        return <>

            <title>{`${ThemeInfo.wallName} | ${brandName}`}</title>

            {matchDomain && widgetMetaObj && Object.keys(widgetMetaObj).length && widgetMetaObj.map((Item) => {
                return <>
                    <meta name={`${Item.property}`} content={Item.content} />
                    <meta name={`og:${Item.property}`} content={Item.content} />
                </>
            })
            }

            {matchDomain && hashtagObj && Object.keys(hashtagObj).length && hashtagObj.map((Item) => {
                return <>
                    <meta name={`${Item.property}`} content={Item.content} />
                    <meta name={`og:${Item.property}`} content={Item.content} />
                </>
            })
            }

            {wall.ThemeInfo && wall.ThemeInfo.gtStatus ?
                <script>
                    {`window.dataLayer = window.dataLayer || [];
                    function gtag() {
                        dataLayer.push(arguments);
                    }
                    gtag("js", new Date());
                    window.gaTokenClient = '${wall.ThemeInfo.gtCode}';
                    gtag("event", "page_view", {
                        "send_to": window.gaTokenClient,
                        "anonymize_ip": true,
                    });`}
                </script> : null}

            {
                isEmbed && wall.ThemeInfo && wall.ThemeInfo.gtStatus ? <script async defer src="https://www.googletagmanager.com/gtag/js" onLoad={() => console.log('script loaded')} />
                    : null
            }

            {
                allFonts && Array.isArray(allFonts) && allFonts.map((href, key) => {
                    return <link key={key} rel="stylesheet" href={`${CloudUrl}/web-fonts/${href}.css`} />
                })
            }

            {wall.ThemeInfo.ownerId == 132857 && isTagbox ? addCustomLang() : null}
            {InHeritStyleRender(wall)}
            {customCssAdd(wall)}
        </>
    }
}

export default AppHead;